import React, { Fragment } from "react";
import Media from "@/components/Media/Media";
import Navigator from "@/components/Navigator/Navigator";
import { Button } from "@nulib/design-system";
import { Canvas, IIIFExternalWebResource } from "@iiif/presentation-3";
import {
  Content,
  Main,
  CollapsibleTrigger,
  CollapsibleContent,
  MediaWrapper,
  Aside,
} from "@/components/Viewer/Viewer.styled";
import { LabeledResource } from "@/hooks/use-iiif/getSupplementingResources";
import Painting from "../Painting/Painting";
import Clip from "@/components/Navigator/Clip";
import { useViewerState } from "@/context/viewer-context";
import { Label } from "@samvera/nectar-iiif";

interface Props {
  activeCanvas: string;
  painting: IIIFExternalWebResource;
  resources: LabeledResource[];
  clips: LabeledResource[];
  items: Canvas[];
  isAbout: boolean;
  isInformation: boolean;
  isMedia: boolean;
  isNavigator: boolean;
  isNavigatorOpen: boolean;
  isPdf: boolean;
  mediaThumbnailPosition: "top" | "bottom";
}

const ViewerContent: React.FC<Props> = ({
  activeCanvas,
  painting,
  resources,
  clips,
  items,
  isAbout,
  isInformation,
  isNavigator,
  isNavigatorOpen,
  isMedia,
  isPdf,
  mediaThumbnailPosition,
}) => {
  const viewerState: any = useViewerState();
  const { configOptions } = viewerState;
  const { renderClips, clipsPosition, renderAbout, renderResources } =
    configOptions;

  const renderMediaThumbnails = () => {
    return (
      <MediaWrapper className="clover-canvases">
        <Media items={items} activeItem={0} />
      </MediaWrapper>
    );
  };

  const renderClipsLists = () => {
    return clips.map((clip, i) => {
      return (
        <Fragment key={i}>
          <Label label={clip.label} />
          <Content key={clip.id}>
            <Clip clip={clip} />
          </Content>
        </Fragment>
      );
    });
  };

  return (
    <Content className="clover-content">
      {renderClips && clips.length > 0 && clipsPosition === "left" && (
        <Aside>{renderClipsLists()}</Aside>
      )}
      <Main>
        {items.length > 1 &&
          mediaThumbnailPosition === "top" &&
          renderMediaThumbnails()}

        {renderClips &&
          clips.length > 0 &&
          clipsPosition === "top" &&
          renderClipsLists()}

        <Painting
          activeCanvas={activeCanvas}
          isMedia={isMedia}
          painting={painting}
          resources={resources}
          isPdf={isPdf}
        />

        {isNavigator && (
          <CollapsibleTrigger>
            <Button as="span">
              {isNavigatorOpen ? "View Items" : "More Information"}
            </Button>
          </CollapsibleTrigger>
        )}
        {items.length > 1 &&
          mediaThumbnailPosition === "bottom" &&
          renderMediaThumbnails()}

        {renderClips &&
          clips.length > 0 &&
          clipsPosition === "bottom" &&
          renderClipsLists()}
      </Main>

      {isInformation &&
        (isAbout || isNavigator) &&
        (renderAbout || renderResources) && (
          <Aside>
            <CollapsibleContent>
              <Navigator activeCanvas={activeCanvas} resources={resources} />
            </CollapsibleContent>
          </Aside>
        )}
    </Content>
  );
};

export default ViewerContent;
